<template>
	<view>
		<!--导航-->
		<uniNavBar :fixed="true" :border="false" :statusBar="true" @clickRight="openAddInput">
			<view class="flex align-center justify-center font-weight-bold w-100">
				<view
					@click="changeTab(index)"
					:class="tabIndex === index ? 'font-lg text-main' : ''"
					class="font-md text-light-muted mx-1"
					v-for="(item, index) in tabBars"
					:key="index"
				>
					{{ item.name }}
				</view>
			</view>
			<text slot="right" class="iconfont icon-fatie_icon"></text>
		</uniNavBar>

		<swiper class="swiper" :current="tabIndex" :duration="150" :style="'height:' + scrollH + 'px;'" @change="onChangeTab">
			<!--关注-->
			<swiper-item >
				<scroll-view scroll-y="true" :style="'height:' + scrollH + 'px;'"
				@scrolltolower="loadmoreEvent">
					<block v-for="(item, index) in list" :key="index">
						<commonList :item="item" :index="index" @doSupport="doSupport"></commonList>
						<divider></divider>
					</block>
					<loadMore :loadmore="loadmore"></loadMore>
				</scroll-view>
			</swiper-item>
			<!--话题-->
			<swiper-item>
				<scroll-view scroll-y="true" :style="'height:' + scrollH + 'px;'">
					<!--热门分类-->
					<hotCate :hotCate="hotCate"></hotCate>
					<!--搜索框-->
					<view class="p-2">
						<view class="bg-light rounded flex align-center justify-center py-2 text-secondary"
						@click="openSearch">
							<text class="iconfont icon-sousuo mr-2"></text>
							搜索话题
						</view>
					</view>
					<!--轮播图-->
					<swiper class="px-2 pb-2" :indicator-dots="true" :autoplay="true" :interval="3000" :duration="1000">
						<swiper-item>
							<image src="/static/bgimg/1.jpg"style="height: 300rpx;" class="w-100 rounded" mode=""></image>
						</swiper-item>
					</swiper>
					<divider></divider>
					<!--最近更新-->
					<view class="p-2 font-md">
						最近更新
					</view>
					<!--话题组件列表-->
					<block v-for="(item,index) in topicList" :key="index">
						<topicList :item="item" :index="index"></topicList>
					</block>
					
				</scroll-view>
			</swiper-item>
		</swiper>
	</view>
</template>

<script>
const demo = [
	{
		username: '昵称',
		userpic: '../../static/default.jpg',
		newstime: '2019-10-20 下午04:30',
		isFollow: true,
		title: '我是标题',
		titlepic: '../../static/demo/datapic/11.jpg',
		support: {
			type: 'support',
			support_count: 1,
			unsupport_count: 0
		},
		comment_count: 2,
		share_num: 2
	},
	{
		username: '昵称',
		userpic: '../../static/default.jpg',
		newstime: '2019-10-20 下午04:30',
		isFollow: true,
		title: '我是标题',
		titlepic: '../../static/demo/datapic/11.jpg',
		support: {
			type: '',
			support_count: 0,
			unsupport_count: 0
		},
		comment_count: 2,
		share_num: 2
	},
	{
		username: '昵称',
		userpic: '../../static/default.jpg',
		newstime: '2019-10-20 下午04:30',
		isFollow: true,
		title: '我是标题',
		titlepic: '../../static/demo/datapic/11.jpg',
		support: {
			type: '',
			support_count: 0,
			unsupport_count: 0
		},
		comment_count: 2,
		share_num: 2
	}
];

import uniNavBar from '@/components/uni-ui/uni-nav-bar/uni-nav-bar.vue';
import commonList from '@/components/common/common_list.vue';
import loadMore from '@/components/common/load_more.vue';
import hotCate from '@/components/news/hot-cate.vue';
import topicList from '@/components/news/topic-list.vue'
export default {
	components: {
		uniNavBar,
		commonList,
		loadMore,
		hotCate,
		topicList
	},
	data() {
		return {
			scrollH: 500,
			tabIndex: 0,
			tabBars: [{ name: '关注' }, { name: '话题' }],
			// 关注列表
			list: [],
			// 1.上拉加载更多 2.加载中... 3.没有更多了
			loadmore : "上拉加载更多",
			
			hotCate : [{
				name : "关注"
			},{
				name : "推荐"
			},{
				name : "体育"
			},{
				name : "热点"
			},{
				name : "财经"
			},{
				name : "娱乐"
			}],
			
			topicList : [{
				cover : "/static/bgimg/1.jpg",
				title : "话题名称",
				desc : "话题描述",
				today_count : 0,
				news_count : 10
			},{
				cover : "/static/bgimg/1.jpg",
				title : "话题名称",
				desc : "话题描述",
				today_count : 0,
				news_count : 10
			},
			{
				cover : "/static/bgimg/1.jpg",
				title : "话题名称",
				desc : "话题描述",
				today_count : 0,
				news_count : 10
			},
			{
				cover : "/static/bgimg/1.jpg",
				title : "话题名称",
				desc : "话题描述",
				today_count : 0,
				news_count : 10
			},
			{
				cover : "/static/bgimg/1.jpg",
				title : "话题名称",
				desc : "话题描述",
				today_count : 0,
				news_count : 10
			}]
		};
	},
	onLoad() {
		uni.getSystemInfo({
			success: res => {
				this.scrollH = res.windowHeight - res.statusBarHeight - 44;
			}
		});
		// 加载测试数据
		this.list = demo;
	},
	methods: {
		openAddInput() {
			uni.navigateTo({
				url: '../add-input/add-input'
			});
		},
		changeTab(index) {
			this.tabIndex = index;
		},
		// 滑动
		onChangeTab(e){
			console.log(e.detail.current)
			this.tabIndex = e.detail.current
		},
		doSupport(e){
			// console.log(e)
			let item = this.list[e.index];
			let msg = e.type === "support" ? "点赞" : "反对";
			if (item.support.type === '') {
				item.support.type = e.type;
				item.support[e.type + '_count']++;
			}
			if (item.support.type === 'support' && e.type === 'unsupport') {
				item.support.support_count--;
				item.support.unsupport_count++;
			} else if (item.support.type === 'unsupport' && e.type === 'support') {
				item.support.support_count++;
				item.support.unsupport_count--;
			}
			item.support.type = e.type;
			uni.showToast({
				title: msg + "成功"
			})
		},
		// 上拉加载
		loadmoreEvent(){
			// 判断是否处于加载中
			if(this.loadmore !== '上拉加载更多'){
				return ;
			}
			// 设置加载更多
			this.loadmore = '加载中...'
			// 模拟请求
			setTimeout(() => {
				// 加载数据
				this.list = [...this.list,...this.list]
				// 设置加载状态
				this.loadmore = '上拉加载更多'
			},2000)
		},
		// 搜索框
		openSearch(){
			uni.navigateTo({
				url:'../search/search?type=topic'
			})
		}
	}
};
</script>

<style></style>
